.header__logo {
  display: block;
  width: 142px;
  height: 33px;
  background-image: url("../../../images/logo.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width:901px) {
  .header__logo {
    margin-left: 19px;
  }
}

@media screen and (max-width:320px) {
  .header__logo {
    margin-left: 27px;
    width: 103px;
    height: 24px;
  }
}
