.profile__avatar-layout {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #000;
  opacity: 0;
  transition: opacity 0.5s ease;
}

.profile__avatar-container:hover .profile__avatar-layout {
  opacity: 0.8;
}
